
cc.Class({
    extends: cc.Component,

    properties: {
        yearLabel:cc.Label,
    },

    onLoad () {
        this.time=0;
        this.season="春";
        this.yearTime=15;
    },

    timeFlow(){
        this.time++;
        switch(this.time%4){
            case 0:this.season="春";break;
            case 1:this.season="夏";break;
            case 2:this.season="秋";break;
            case 3:this.season="冬";break;
        }
        this.yearTime=Math.floor(this.time/4)+15;
        this.yearLabel.string="XX"+this.yearTime+this.season;
    },


});
