
let roleMap = {
    1:{
        name:'教员',
    },
    2:{
        name:'你',
    },

};

cc.Class({
    extends: cc.Component,

    properties: {
        nameLabel:cc.Label,

        textLabel:cc.Label,

        academyNode:cc.Node,

        but_1: cc.Node,

        but_2: cc.Node,

    },

    onLoad(){


        window.dialog = this.node;

        cc.systemEvent.on('keydown',this.onKeyDown,this);
    },

    onDestroy(){
        cc.systemEvent.off('keydown',this.onKeyDown,this);
    },

    init_but(){
        this.init([
            {role:1,content:'先生，只靠政府人员是不够的。'},
            {role:1,content:'如果您想尽快实现碳达峰乃至碳中和，您必须让您的人民共同努力。'},
            {role:1,content:'宣传和教育的作用必不可少。'},
            {role:1,content:'我们的宣传及教育研究会会提出一些草案供您选择，一些可能需要一些投资，一些可能事关重大。'},
            {role:1,content:'为避免误导群众，请您谨慎选择。'},
            {role:2,content:'我明白。'},

        ]);
    },
    onKeyDown(e){
        switch(e.keyCode){
            case cc.macro.KEY.space:{
                this.updateStop();
                break;
            }
        }
    },

    init(textDataArr){
        this.node.active = true;
        this.but_1.active=false;
        //当前文本
        this.nowText = null;
        //是否播完
        this.textEnd = true;
        //播放的总时长
        this.tt = 0;

        this.textIndex = -1;
        this.textDataArr = textDataArr;

        this.nextTextData();
    },
    //按空格继续下一组对话的播放，不然退出
    nextTextData(){
        if(!this.textEnd)return;
        if(++this.textIndex<this.textDataArr.length){
            this.setTextData(this.textDataArr[this.textIndex]);
        }else{
            this.closeDialog();
        }
    },

    //改变内容
    setTextData(textData){
        if(!this.textEnd)return;
        this.textEnd = false;

        this.nameLabel.string= roleMap[textData.role].name;
        this.textLabel.string= '';
        this.nowText=textData.content;
    },
    //隐藏对话框节点并出现其他建筑按钮
    closeDialog(){
        this.node.destroy();
        this.academyNode.active=true;
        this.but_2.active=true;
    },
    //跳过打字机，直接全部输出。
    updateStop(){
        if(this.textLabel.string.length < this.nowText.length){
            this.textLabel.string=this.nowText;
        }else{
            this.nowText="";
            this.nextTextData();
        }
    },
    //随帧数的更新而更新文字，实现打字机效果
    update(dt){
        if(!this.nowText)return;
        this.tt += dt;
        //等待tt时间后打出下一个字符
        if(this.tt >= 0.05){
            if(this.textLabel.string.length < this.nowText.length){
                this.textLabel.string=this.nowText.slice(0,this.textLabel.string.length+1);

            }else{
                this.textEnd=true;

            }
            this.tt=0;
        }
    }
});
