
cc.Class({
    extends: cc.Component,

    properties: {
        statistic_1Label:cc.Label,
        statistic_2Label:cc.Label,
        statistic_3Label:cc.Label,
        statistic_4Label:cc.Label,
        boardNode:cc.Node,
        gameNode:cc.Node,
    },

    onLoad () {

        this.statistic_1Label.string="环境：差";
        this.statistic_2Label.string="经济：一般";
        this.statistic_3Label.string="气候舆论：低下";
        this.statistic_4Label.string="能、产结构：较低";
    },

    updateBoard() {
        this.getScore();

        if (this.score1 < 10) {
            this.statistic_1Label.string = "环境：灭亡边缘";
        } else if (this.score1 < 20) {
            this.statistic_1Label.string = "环境：还来得及！";
        } else if (this.score1 < 30) {
            this.statistic_1Label.string = "环境：灾难横行";
        } else if (this.score1 < 40) {
            this.statistic_1Label.string = "环境：天气诡异";
        } else if (this.score1 < 50) {
            this.statistic_1Label.string = "环境：差";
        } else if (this.score1 < 60) {
            this.statistic_1Label.string = "环境：一般";
        } else if (this.score1 < 70) {
            this.statistic_1Label.string = "环境：良好";
        } else if (this.score1 < 80) {
            this.statistic_1Label.string = "环境：优异";
        }else if(this.score1<90){
            this.statistic_1Label.string="环境：远古入侵";
        }else{
            this.statistic_1Label.string="环境：灵气复苏";
        }


        if (this.score2 < 10) {
            this.statistic_2Label.string = "经济：崩溃边缘";
        } else if (this.score2 < 20) {
            this.statistic_2Label.string = "经济：振奋精神！";
        } else if (this.score2 < 30) {
            this.statistic_2Label.string = "经济：大萧条";
        } else if (this.score2 < 40) {
            this.statistic_2Label.string = "经济：危机四伏";
        } else if (this.score2 < 50) {
            this.statistic_2Label.string = "经济：一般";
        } else if (this.score2 < 60) {
            this.statistic_2Label.string = "经济：欣欣向荣";
        } else if (this.score2 < 70) {
            this.statistic_2Label.string = "经济：资本横行";
        } else if (this.score2 < 80) {
            this.statistic_2Label.string = "经济：繁荣开放";
        } else if (this.score2 < 90) {
            this.statistic_2Label.string="经济：无可匹敌";
        }else{
            this.statistic_2Label.string="经济：世界中心";
        }



        if (this.score3 < 10) {
            this.statistic_3Label.string = "气候舆论：...";
        } else if (this.score3 < 20) {
            this.statistic_3Label.string = "气候舆论：啊？";
        } else if (this.score3 < 30) {
            this.statistic_3Label.string = "气候舆论：那是什么？";
        } else if (this.score3 < 40) {
            this.statistic_3Label.string = "气候舆论：漠不关心";
        } else if (this.score3 < 50) {
            this.statistic_3Label.string = "气候舆论：小范围的讨论";
        } else if (this.score3 < 60) {
            this.statistic_3Label.string = "气候舆论：社会有所关注";
        } else if (this.score3 < 70) {
            this.statistic_3Label.string = "气候舆论：社会有所讨论";
        } else if (this.score3 < 80) {
            this.statistic_3Label.string = "气候舆论：人民相当关注";
        }else if(this.score3<90){
            this.statistic_3Label.string="气候舆论：人民奋起呼吁";
        }else{
            this.statistic_3Label.string="气候舆论：全民对此狂热呼喊";
        }



        if (this.score4 < 10) {
            this.statistic_4Label.string = "能、产结构：偏科致死";
        } else if (this.score4 < 20) {
            this.statistic_4Label.string = "能、产结构：还能救救";
        } else if (this.score4 < 30) {
            this.statistic_4Label.string = "能、产结构：畸形儿";
        } else if (this.score4 < 40) {
            this.statistic_4Label.string = "能、产结构：有所倾斜";
        } else if (this.score4 < 50) {
            this.statistic_4Label.string = "能、产结构：发展正常";
        } else if (this.score4 < 60) {
            this.statistic_4Label.string = "能、产结构：正均衡转移";
        } else if (this.score4 < 70) {
            this.statistic_4Label.string = "能、产结构：较好";
        } else if (this.score4 < 80) {
            this.statistic_4Label.string = "能、产结构：全产业齐全";
        }else if(this.score4 < 90){
            this.statistic_4Label.string="能、产结构：拥有一切";
        }else{
            this.statistic_4Label.string="能、产结构：世界工厂";
        }


        this.boardNode.active=true;
    },


    getScore(){
        this.score1=this.gameNode.getComponent('game').score1;
        this.score2=this.gameNode.getComponent('game').score2;
        this.score3=this.gameNode.getComponent('game').score3;
        this.score4=this.gameNode.getComponent('game').score4;
    },

    close(){
        this.boardNode.active=false;
    }


});
