
let roleMap = {
    1:{
        name:'侍者',
    },
    2:{
        name:'你',
    },
    3:{
        name:'教授',
    },

};

cc.Class({
    extends: cc.Component,

    properties: {
        nameLabel:cc.Label,

        textLabel:cc.Label,

        but_1: cc.Node,

        but_2:cc.Node,

        choice_1: cc.Node,

        choice_2:cc.Node,

        choice_3:cc.Node,

        choice_4:cc.Node,

        lenNode:cc.Node,

        rulerNode:cc.Node,
    },

    onLoad(){


        window.dialog = this.node;

        cc.systemEvent.on('keydown',this.onKeyDown,this);
    },

    onDestroy(){
        cc.systemEvent.off('keydown',this.onKeyDown,this);
    },

    init_but(){
        this.init([
            {role:2,content: '教授，我想要关于碳中和咨询你一些事。你有兴趣谈谈吗？'},
            {role:3,content: '当然，我正要找你确认一些事情。'},
            {role:3,content: '将全球温升稳定在一个给定的水平意味着全球“净”温室气体排放需要大致下降到零。'},
            {role:3,content: '即在进入大气的温室气体排放和吸收的汇之间达到平衡。'},
            {role:3,content: '这一平衡通常被称为中和(neutrality) 或净零排放 (net-zero emissions)。'},
            {role:3,content:'在开始你的措施前，你必须将你的政府的承诺或者声明精确化。'},
            {role:3,content:'你需要知道，在各种“中和”中，“气候中和”与“碳中和”尽管相似但却不同。'},
            {role:3,content:'“碳”代表的也不只是二氧化碳气体，还有其他的温室气体也要一并解决。'},
            {role:3,content:'这两种概念有着许多微妙的差别。'},
            {role:3,content: '但是你现在只需要明白，将源头解决（碳中和）与将影响解决（气候中和）是不一样的。'},
            {role:3,content:'但是你实现其中任何一个都是相当的成功。'},
            {role:3,content:'那么你想要你的政府的承诺和声明是什么样子的？这取决于你对你的国家整体实力的评估。'},
            //序列11
            {role:3,content:'很好的选择，先生。'},
            {role:3,content:'但是无论你选择哪一个，我们今后的选择必须尽可能少犯错且执行到位。'},


            {role:3,content:'除此之外，我们还需要确定我们所用来衡量温室气体增温能力的指标。'},
            {role:3,content:'目前有两种主流的指标：全球增温潜势(GWP)和全球温变潜势(GTP)。'},
            {role:3,content:'好好听着，先生。'},
            {role:3,content:'全球增温潜势(GWP)，是在等量等时间内某种温室气体与二氧化碳产生的辐射强迫的比值，' +
                    '从而将温室气体都折合成等效的二氧化碳，方便评估与规划。这个指标目前最为成熟且普遍。'},
            {role:3,content:'而全球温变潜势(GTP)，是依据温室气体对全球平均地表温度的变化的影响来折合。' +
                    '理论上来说更精确，但是测量上有许多不确定因素，在技术与科学验证上尚不如GWP成熟可靠。'},
            {role:3,content:'就像我说的，无论选择哪一个，最终的结果都取决于我们的选择。'},
            {role:3,content:'那么你的选择是？'},
            //序列20
            {role:3,content:'很好，有决断才能领导接下来的行动。'},


            {role:3,content:'如果你没有问题，就可以开始我们的行动了'},
            {role:3,content:'你最好先拜访一遍我所标出的地图上的每一个地方，那里的人们将有助于我们实现目标。'},
            {role:3,content:'当然，根据今后的需要，你可以专注于其中的某一处或者几处。'},
            {role:3,content:'成立的监察小组将帮助你了解国家和世界的实时状态。'},
            {role:1,content: '感谢您，教授。'},
            {role:1,content: '先生，我将来辅助您接下来的政府工作，'},
            {role:1,content: '我会帮您整理有争议的政策的决议，以供您选择。'},
            {role:1,content: '这些关乎国家发展的政策，大部分都将需要大范围的较高额投资。'},
            {role:1,content: '或者需要您谨慎确认发布的时机。'},
            {role:1,content: '希望您务必慎重行事。'},
            {role:2,content: '当然，我明白。'},

        ]);
    },
    onKeyDown(e){
        switch(e.keyCode){
            case cc.macro.KEY.space:{
                this.updateStop();
                break;
            }
        }
    },

    init(textDataArr){
        this.node.active = true;
        this.but_1.active=false;
        //当前文本
        this.nowText = null;
        //是否播完
        this.textEnd = true;
        //播放的总时长
        this.tt = 0;

        this.textIndex = -1;
        this.textDataArr = textDataArr;

        this.nextTextData();
    },
    //按空格继续下一组对话的播放，不然退出
    nextTextData(){
        if(!this.textEnd)return;
        this.choice_1.active=false;
        this.choice_2.active=false;
        this.choice_3.active=false;
        this.choice_4.active=false;

        if(this.textIndex==10){
            this.choice_1.active=true;
            this.choice_2.active=true;
        }
        if(this.textIndex==19){
            this.choice_3.active=true;
            this.choice_4.active=true;
        }

        if(++this.textIndex<this.textDataArr.length){
            this.setTextData(this.textDataArr[this.textIndex]);
        }else{
            this.closeDialog();
        }
    },

    //改变内容
    setTextData(textData){
        if(!this.textEnd)return;
        this.textEnd = false;

        this.nameLabel.string= roleMap[textData.role].name;
        this.textLabel.string= '';
        this.nowText=textData.content;
    },
    //隐藏对话框节点并出现其他建筑按钮
    closeDialog(){
        this.node.destroy();

        this.lenNode.active=true;
        this.rulerNode.active=true;
        this.but_2.active=true;
    },

    updateStop(){
        if(this.textLabel.string.length < this.nowText.length){
            this.textLabel.string=this.nowText;
        }else{
            this.nowText="";
            this.nextTextData();
        }
    },
    //随帧数的更新而更新文字，实现打字机效果
    update(dt){
        if(!this.nowText)return;
        this.tt += dt;
        //等待tt时间后打出下一个字符
        if(this.tt >= 0.05){
            if(this.textLabel.string.length < this.nowText.length){
                this.textLabel.string=this.nowText.slice(0,this.textLabel.string.length+1);

            }else{
                this.textEnd=true;

            }
            this.tt=0;
        }
    }
});

