
let roleMap = {
    1:{
        name:'侍者',
    },
    2:{
        name:'你',
    },
    3:{
        name:'教授',
    },

};

cc.Class({
    extends: cc.Component,

    properties: {
        nameLabel:cc.Label,

        textLabel:cc.Label,

        but_1: cc.Node,

        but_2: cc.Node,

    },

    onLoad(){


        window.dialog = this.node;

        cc.systemEvent.on('keydown',this.onKeyDown,this);
    },

    onDestroy(){
        cc.systemEvent.off('keydown',this.onKeyDown,this);
    },

    init_but(){
        this.init([
            {role:1,content:'先生，有位教授想要见您，他说他对您宣传的理念很感兴趣。'},
            {role:1,content:'要让他进来吗？'},
            {role:2,content:'当然，请他进来。'},
            {role:3,content:'我就知道这个世界还有救！阁下，你必须任用我！'},
            {role:3,content:'从博士论文开始我就一直在调查气候变化，没有人比我更了解它！'},
            {role:3,content:'我知道你跟那些蠢蛋不一样，但如果你真想拯救这个世界，你必须听取我的建议！'},
            {role:3,content:'让我来辅助你！'},
            {role:2,content:'当然，如果你愿意为我提供建议。'},
            {role:3,content:'明智之举。'},
        ]);


    },
    onKeyDown(e){
        switch(e.keyCode){
            case cc.macro.KEY.space:{
                this.updateStop();
                break;
            }
        }
    },

    init(textDataArr){
        this.node.active = true;
        this.but_1.active=false;
        //当前文本
        this.nowText = null;
        //是否播完
        this.textEnd = true;
        //播放的总时长
        this.tt = 0;

        this.textIndex = -1;
        this.textDataArr = textDataArr;

        this.nextTextData();
    },
    //按空格继续下一组对话的播放，不然退出
    nextTextData(){
        if(!this.textEnd)return;
        if(++this.textIndex<this.textDataArr.length){
            this.setTextData(this.textDataArr[this.textIndex]);
        }else{
            this.closeDialog();
        }
    },

    //改变内容
    setTextData(textData){
        if(!this.textEnd)return;
        this.textEnd = false;

        this.nameLabel.string= roleMap[textData.role].name;
        this.textLabel.string= '';
        this.nowText=textData.content;
    },
    //隐藏对话框节点并出现其他建筑按钮
    closeDialog(){
        this.node.destroy();
        this.but_2.active=true;
    },
    //跳过打字机，直接全部输出。
    updateStop(){
        if(this.textLabel.string.length < this.nowText.length){
            this.textLabel.string=this.nowText;
        }else{
            this.nowText="";
            this.nextTextData();
        }
    },
    //随帧数的更新而更新文字，实现打字机效果
    update(dt){
        if(!this.nowText)return;
        this.tt += dt;
        //等待tt时间后打出下一个字符
        if(this.tt >= 0.05){
            if(this.textLabel.string.length < this.nowText.length){
                this.textLabel.string=this.nowText.slice(0,this.textLabel.string.length+1);

            }else{
                this.textEnd=true;

            }
            this.tt=0;
        }
    }
});
