
cc.Class({
    extends: cc.Component,

    properties: {
        statisticLabel:cc.Label,
        boardNode:cc.Node,
        closeAudio:{
            default:null,
            type:cc.AudioClip,
        }
    },
    
    onlLoad(){

        this.statisticLabel.string="";
    },
    spring(arg1,arg2,arg3,arg4){
        this.boardNode.active=true;
        if(arg1>0){
            this.statisticLabel.string+="世界碳排放速率上升了。";
        }else{
            this.statisticLabel.string+="世界碳排放速率下降了。";
        }
        
        if(arg2>0){
            this.statisticLabel.string+="你的国家经济向好发展。";
        }else{
            this.statisticLabel.string+="你的国家经济状况恶化了。";
        }
        
        if(arg3>0){
            this.statisticLabel.string+="舆论对碳排放关注度提升了。";
        }else{
            this.statisticLabel.string+="舆论对碳排放关注度下降了。";
        }
        
        if(arg4>0){
            this.statisticLabel.string+="你的能源或产业结构有所优化";
        }else{
            this.statisticLabel.string+="你的国家能源或产业结构有所退化。";
        }
    },

    closeBoard(){
        this.boardNode.active=false;
        this.statisticLabel.string="";
        cc.audioEngine.playEffect(this.closeAudio,false);
    }
    


});
