
let roleMap = {
    1:{
        name:'未知来源的声音',
    },
    2:{
        name:'未知来源的声音',
    },
    3:{
        name:'未知来源的声音',
    },
    4:{
        name:'未知来源的声音',
    },

};

cc.Class({
    extends: cc.Component,

    properties: {
        nameLabel:cc.Label,

        textLabel:cc.Label,

        but_1:cc.Node,


    },

    onLoad(){
        // this.init([
        //     {role:1,content:'你穿越到了另一个世界！(按空格键继续)'},
        //     {role:2,content:'这个世界的碳排放已经严重超标！生态环境遭到了极大破坏！(按空格键继续)'},
        //     {role:3,content:'如果放任下去，人类将会灭绝！(按空格键继续)'},
        //     {role:3,content:'放下你手中的快乐水吧，身为这个世界的某个大国的大人物，你必须制止这一切！(按空格键继续)'},
        //     {role:4,content:'但是无知的外国人只想着利益，他们对你的国家虎视眈眈！(按空格键继续)'},
        //     {role:4,content:'你必须做出足够明智的选择，拯救世界，同时发展你的国家！(按空格键继续)'},
        // ]);

        window.dialog = this.node;

        cc.systemEvent.on('keydown',this.onKeyDown,this);
    },

    onDestroy(){
        cc.systemEvent.off('keydown',this.onKeyDown,this);
    },

    onKeyDown(e){
        switch(e.keyCode){
            case cc.macro.KEY.space:{
                this.updateStop();
                break;
            }
        }
    },

    init(textDataArr){
        //当前文本
        this.nowText = null;
        //是否播完
        this.textEnd = true;
        //播放的总时长
        this.tt = 0;

        this.textIndex = -1;
        this.textDataArr = textDataArr;
        this.node.active = true;
        this.nextTextData();
    },
    //按空格继续下一组对话的播放，不然退出
    nextTextData(){
        if(!this.textEnd)return;
        if(++this.textIndex<this.textDataArr.length){
            this.setTextData(this.textDataArr[this.textIndex]);
        }else{
            this.closeDialog();
        }
    },

    //改变内容
    setTextData(textData){
        if(!this.textEnd)return;
        this.textEnd = false;

        this.nameLabel.string= roleMap[textData.role].name;
        this.textLabel.string= '';
        this.nowText=textData.content;
    },
    //隐藏对话框节点并出现其他建筑按钮
    closeDialog(){

        this.but_1.active=true;
        console.log(1);
        this.node.destroy();
    },

    updateStop(){
        if(this.textLabel.string.length < this.nowText.length){
            this.textLabel.string=this.nowText;
        }else{
            this.nowText="";
            this.nextTextData();
        }
    },
    //随帧数的更新而更新文字，实现打字机效果
    update(dt){
        if(!this.nowText)return;
        this.tt += dt;
        //等待tt时间后打出下一个字符
        if(this.tt >= 0.05){
            if(this.textLabel.string.length < this.nowText.length){
                this.textLabel.string=this.nowText.slice(0,this.textLabel.string.length+1);

            }else{
                this.textEnd=true;

            }
            this.tt=0;
        }
    }
});
