
let roleMap = {
    1:{
        name:'外交部发言人',
    },
    2:{
        name:'你',
    },

};

cc.Class({
    extends: cc.Component,

    properties: {
        nameLabel:cc.Label,

        textLabel:cc.Label,

        ambassyNode:cc.Node,

        but_1: cc.Node,
    },

    onLoad(){


        window.dialog = this.node;

        cc.systemEvent.on('keydown',this.onKeyDown,this);
    },

    onDestroy(){
        cc.systemEvent.off('keydown',this.onKeyDown,this);
    },

    init_but(){
        this.init([
            {role:1,content:'先生，如果您想拯救这个世界，只靠我们的国家实现碳中和是不够的，必须协同世界各国一起实现。'},
            {role:1,content:'即便联合，各国也并非真的能完全同心协力。'},
            {role:1,content:'尽管您是个高尚的人，但是国外的部分政客会绞尽脑汁地打压我们、攫夺利益。'},
            {role:1,content:'希望您务必对此清醒。'},
            {role:2,content:'当然，我会权衡好这之间的关系。'},
            {role:1,content:'我会整理好您的理念并进行宣传。同时国际上的一些重要合作与交流，我会关注并提交给您以供决策。'},
            {role:2,content:'我清楚了。'},
        ]);
    },
    onKeyDown(e){
        switch(e.keyCode){
            case cc.macro.KEY.space:{
                this.updateStop();
                break;
            }
        }
    },

    init(textDataArr){
        this.node.active = true;
        this.but_1.active=false;
        //当前文本
        this.nowText = null;
        //是否播完
        this.textEnd = true;
        //播放的总时长
        this.tt = 0;

        this.textIndex = -1;
        this.textDataArr = textDataArr;

        this.nextTextData();
    },
    //按空格继续下一组对话的播放，不然退出
    nextTextData(){
        if(!this.textEnd)return;
        if(++this.textIndex<this.textDataArr.length){
            this.setTextData(this.textDataArr[this.textIndex]);
        }else{
            this.closeDialog();
        }
    },

    //改变内容
    setTextData(textData){
        if(!this.textEnd)return;
        this.textEnd = false;

        this.nameLabel.string= roleMap[textData.role].name;
        this.textLabel.string= '';
        this.nowText=textData.content;
    },
    //隐藏对话框节点并出现其他建筑按钮
    closeDialog(){
        this.node.destroy();
        this.ambassyNode.active=true;

    },
    //跳过打字机，直接全部输出。
    updateStop(){
        if(this.textLabel.string.length < this.nowText.length){
            this.textLabel.string=this.nowText;
        }else{
            this.nowText="";
            this.nextTextData();
        }
    },
    //随帧数的更新而更新文字，实现打字机效果
    update(dt){
        if(!this.nowText)return;
        this.tt += dt;
        //等待tt时间后打出下一个字符
        if(this.tt >= 0.05){
            if(this.textLabel.string.length < this.nowText.length){
                this.textLabel.string=this.nowText.slice(0,this.textLabel.string.length+1);

            }else{
                this.textEnd=true;

            }
            this.tt=0;
        }
    }
});
