
let roleMap = {
    1:{
        name:'顾问',
    },
    2:{
        name:'你',
    },

};

cc.Class({
    extends: cc.Component,

    properties: {
        nameLabel:cc.Label,

        textLabel:cc.Label,

        economyNode:cc.Node,

        but_1: cc.Node,

        but_2: cc.Node,

    },

    onLoad(){


        window.dialog = this.node;

        cc.systemEvent.on('keydown',this.onKeyDown,this);
    },

    onDestroy(){
        cc.systemEvent.off('keydown',this.onKeyDown,this);
    },

    init_but(){
        this.init([
            {role:1,content:'您的远见令人钦佩，先生。'},
            {role:1,content:'但是我不得不提醒您，我国国家经济实力还相对薄弱。'},
            {role:1,content:'国外的资本家们对我们虎视眈眈。'},
            {role:1,content:'国民们肯定不会同意牺牲国家利益来控制气候变化。'},
            {role:1,content:'因此在经济上您也得认真考虑。'},
            {role:1,content:'这也是为了制衡资本家们，防止他们将不合理的减排压力转嫁给我们。'},
            {role:2,content:'我明白，我会有所权衡，审慎考虑。'},
            {role:1,content:'希望您三思而行。'},
            {role:1,content:'我会整体挑选一些经济决策上的关键方案，以供您调整部署。'},
        ]);
    },
    onKeyDown(e){
        switch(e.keyCode){
            case cc.macro.KEY.space:{
                this.updateStop();
                break;
            }
        }
    },

    init(textDataArr){
        this.node.active = true;
        this.but_1.active=false;
        //当前文本
        this.nowText = null;
        //是否播完
        this.textEnd = true;
        //播放的总时长
        this.tt = 0;

        this.textIndex = -1;
        this.textDataArr = textDataArr;

        this.nextTextData();
    },
    //按空格继续下一组对话的播放，不然退出
    nextTextData(){
        if(!this.textEnd)return;
        if(++this.textIndex<this.textDataArr.length){
            this.setTextData(this.textDataArr[this.textIndex]);
        }else{
            this.closeDialog();
        }
    },

    //改变内容
    setTextData(textData){
        if(!this.textEnd)return;
        this.textEnd = false;

        this.nameLabel.string= roleMap[textData.role].name;
        this.textLabel.string= '';
        this.nowText=textData.content;
    },
    //隐藏对话框节点并出现其他建筑按钮
    closeDialog(){
        this.node.destroy();
        this.economyNode.active=true;
        this.but_2.active=true;
    },
    //跳过打字机，直接全部输出。
    updateStop(){
        if(this.textLabel.string.length < this.nowText.length){
            this.textLabel.string=this.nowText;
        }else{
            this.nowText="";
            this.nextTextData();
        }
    },
    //随帧数的更新而更新文字，实现打字机效果
    update(dt){
        if(!this.nowText)return;
        this.tt += dt;
        //等待tt时间后打出下一个字符
        if(this.tt >= 0.05){
            if(this.textLabel.string.length < this.nowText.length){
                this.textLabel.string=this.nowText.slice(0,this.textLabel.string.length+1);

            }else{
                this.textEnd=true;

            }
            this.tt=0;
        }
    }
});
